import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";

import { IconButton, Input, Avatar } from "@material-ui/core";
import CachedIcon from "@material-ui/icons/Cached";

import firebase from "firebase";
import db, { auth, provider } from "./Firebase";

import { useDispatch } from "react-redux";
import { setCurrentUser } from "../redux/actions";

const SignupLogin = () => {
   const dispatch = useDispatch();

   const [avatars, setAvatars] = useState([]);
   const [selectedAvatar, setSelectedAvatar] = useState("");
   const [reloadAvatar, setReloadAvatar] = useState(false);

   const [username, setUsername] = useState("");
   const [gender, setGender] = useState("");
   const [errorMsg, setErrorMsg] = useState("");
   const [loginErrorMsg, setLoginErrorMsg] = useState("");

   const [loginEmail, setLoginEmail] = useState("");
   const [signupEmail, setSignupEmail] = useState("");
   const [dbAuthInfo, setDbAuthInfo] = useState([]);

   const radioBtnMale = useRef(null);
   const radioBtnFemale = useRef(null);
   const radioBtnNone = useRef(null);

   useEffect(() => {
      db.collection("allUsers").onSnapshot((snap) => {
         setDbAuthInfo(
            snap.docs.map((doc) => {
               return {
                  id: doc.id,
                  data: doc.data(),
               };
            })
         );
      });
   }, []);

   useEffect(() => {
      setTimeout(() => {
         if (loginEmail.length > 0) {
            let matchingEmail = 0;

            dbAuthInfo.forEach((info) => {
               if (info.data.googleEmail === loginEmail) {
                  matchingEmail += 1;
                  dispatch(
                     setCurrentUser({
                        userID: info.id,
                        email: info.data.googleEmail,
                        username: info.data.username,
                        avatarURL: info.data.avatarURL,
                        isLoggedIn: true,
                     })
                  );
               }
            });

            if (matchingEmail < 1) {
               setLoginErrorMsg(
                  "No account signed up using this gmail.\n Please signup first!"
               );
            } else {
               setLoginErrorMsg("");
            }

            setLoginEmail("");
         }
      }, 500);
   }, [loginEmail, dbAuthInfo]);

   useEffect(() => {
      if (signupEmail.length > 0) {
         let emailCount = 0;
         dbAuthInfo.forEach((info) => {
            if (info.data.googleEmail === signupEmail) {
               emailCount += 1;
            }
         });

         if (emailCount > 0) {
            alert("Account already exist");
         } else {
            db.collection("allUsers").add({
               username: username,
               googleEmail: signupEmail,
               avatarURL: selectedAvatar ? selectedAvatar : "",
               timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            });
            setLoginEmail(signupEmail);
         }

         setSignupEmail("");
      }
   }, [signupEmail]);

   /* Handle gender & radio buttons & avatar */
   useEffect(() => {
      setReloadAvatar(false);
      if (gender) {
         let array = [];
         for (let i = 0; i < 10; i++) {
            const randomNum =
               Math.random() * 5000 + (username ? username : null);

            array.push(
               `https://avatars.dicebear.com/api/${gender}/${randomNum}.svg`
            );
         }
         setAvatars(array);
      }
   }, [gender, reloadAvatar]);

   const handleGender = (e) => {
      const result = e.target.value;
      if (result === "none") {
         setGender("");
         setAvatars([]);
         setSelectedAvatar("");
      } else {
         setGender(result);
      }
   };

   const unCheckAllRadioBtn = () => {
      if (radioBtnMale.current.checked) {
         radioBtnMale.current.checked = false;
      }
      if (radioBtnFemale.current.checked) {
         radioBtnFemale.current.checked = false;
      }
      if (radioBtnNone.current.checked) {
         radioBtnNone.current.checked = false;
      }

      setGender("");
      setAvatars([]);
      setSelectedAvatar("");
   };
   /* Handle gender & radio Buttons end */

   const handleSingup = () => {
      let proceed_to_signup = false;

      if (username.length < 1) {
         proceed_to_signup = false;
         setErrorMsg("Please provide an username");
      } else {
         proceed_to_signup = true;
         setErrorMsg("");
      }

      if (proceed_to_signup) {
         auth
            .signInWithPopup(provider)
            .then((result) => {
               setSignupEmail(result.user.email);
               setUsername("");
               if (selectedAvatar.length > 0) {
                  unCheckAllRadioBtn();
               }
            })
            .catch((err) => alert(err.message));
      }
   };

   const handleLogin = () => {
      auth
         .signInWithPopup(provider)
         .then((result) => {
            setLoginEmail(result.user.email);
         })
         .catch((err) => alert(err.message));
   };

   return (
      <div className="signup_login row">
         <div className="signup_login__wrapper col-lg-8 col-md-10 col-10">
            <div className="wrapper__userInfo-googleSignup">
               <strong className="userInfo__title">Signup:</strong>
               <div className="userInfo__errorMsg text-danger">
                  <p>{errorMsg}</p>
               </div>
               <div className="userInfo__username">
                  <div className="username__avatar">
                     <Avatar
                        src={
                           selectedAvatar
                              ? selectedAvatar
                              : "/static/images/avatar/1.jpg"
                        }
                        alt={username}
                     />
                  </div>
                  <div className="username__input">
                     <Input
                        type="text"
                        placeholder="Username"
                        onChange={(e) => setUsername(e.target.value)}
                        value={username}
                     />
                  </div>
               </div>
               <div className="userInfo__gender">
                  <div className="gender__male">
                     <input
                        type="radio"
                        value="male"
                        name="gender"
                        onChange={handleGender}
                        ref={radioBtnMale}
                     />
                     <span>Male</span>
                  </div>
                  <div className="gender__female">
                     <input
                        type="radio"
                        value="female"
                        name="gender"
                        onChange={handleGender}
                        ref={radioBtnFemale}
                     />
                     <span>Female</span>
                  </div>
                  <div className="gender__none">
                     <input
                        type="radio"
                        value="none"
                        name="gender"
                        onChange={handleGender}
                        ref={radioBtnNone}
                     />
                     <span>None</span>
                  </div>
               </div>
               <div className={avatars.length > 0 ? "row" : null}>
                  <div
                     className={
                        avatars.length > 0
                           ? "userInfo__avatar col-lg-6 col-10 col-md-8"
                           : null
                     }
                  >
                     <div
                        className={
                           avatars.length > 0
                              ? "avatar__reload"
                              : "avatar__reload d-none"
                        }
                     >
                        <strong>Choose Avatar:</strong>
                        <IconButton onClick={() => setReloadAvatar(true)}>
                           <CachedIcon />
                        </IconButton>
                     </div>
                     {avatars
                        ? avatars.map((avatar, index) => {
                             return (
                                <IconButton
                                   key={index}
                                   onClick={() => setSelectedAvatar(avatar)}
                                >
                                   <Avatar type="button" src={avatar} />
                                </IconButton>
                             );
                          })
                        : null}
                  </div>
               </div>
               <div className="row">
                  <Link
                     to="/"
                     className="googleSignup cursor-p col-12"
                     onClick={handleSingup}
                  >
                     <div className="googleSingup__avatar">
                        <Avatar
                           className="avatar"
                           src={`${process.env.PUBLIC_URL}/google_icon.png`}
                        />
                     </div>
                     <div className="googleSignup__text">
                        <strong>Signup With Google</strong>
                     </div>
                  </Link>
               </div>
            </div>
            <div className="wrapper__or">
               <div className="orDiv1"></div>
               <span>OR</span>
               <div className="orDiv2"></div>
            </div>
            <div className="googleLogin__errorMsg text-danger text-center">
               <p>{loginErrorMsg}</p>
            </div>
            <Link
               to="/"
               className="wrapper__googleLogin cursor-p"
               onClick={handleLogin}
            >
               <div className="googleLogin__avatar">
                  <Avatar
                     className="avatar"
                     src={`${process.env.PUBLIC_URL}/google_icon.png`}
                  />
               </div>
               <div className="googleLogin__text">
                  <strong>Login With Google</strong>
               </div>
            </Link>
            <div className="wrapper__creatorName">
               <p>
                  Created by <strong>MD Omar</strong>
               </p>
            </div>
         </div>
      </div>
   );
};

export default SignupLogin;
