import React, { useRef, useState, useEffect } from "react";
import { useParams } from "react-router-dom";

import { Avatar } from "@material-ui/core";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import SendRoundedIcon from "@material-ui/icons/SendRounded";
import PhotoIcon from "@material-ui/icons/Photo";
import { IconButton } from "@material-ui/core";

import firebase from "firebase";
import db, { storage } from "./Firebase";
import { useSelector } from "react-redux";

function MessageBox() {
   const { currentUserInfo } = useSelector((state) => state);

   const [file, setFile] = useState(null);
   const [inputText, setInputText] = useState("");
   const [progress, setProgress] = useState(0);

   const [selectedUser_name, setSelectedUser_name] = useState("");
   const [selectedUser_avatar, setSelectedUser_avatar] = useState(null);
   const [convMessages, setConvMessages] = useState([]);
   const [conversationIDs, setConversationIDs] = useState([]);
   const [currentConvID, setCurrentConvID] = useState(null);

   const fileInputRef = useRef(null);
   const messageBoxRef = useRef(null);
   const { selUserID } = useParams();

   const handleSendBtn = (e) => {
      e.preventDefault();

      if (currentConvID) {
         db.collection("convs").doc(currentConvID).collection("messages").add({
            message: inputText,
            recieverID: selUserID,
            senderID: currentUserInfo.userID,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
         });
      } else {
         console.log("select a conversation first!!");
      }

      setInputText("");
   };

   useEffect(() => {
      const convID1 = selUserID + currentUserInfo.userID;
      const convID2 = currentUserInfo.userID + selUserID;

      if (selectedUser_name.length > 0) {
         let matchingConvID = 0;
         conversationIDs.forEach((dbConvID) => {
            if (dbConvID === convID1 || dbConvID === convID2) {
               setCurrentConvID(dbConvID);
               matchingConvID += 1;
            }
         });

         if (matchingConvID < 1) {
            db.collection("convs")
               .doc(convID2)
               .set({
                  convNameByUsers: `${currentUserInfo.username} & ${selectedUser_name}`,
                  convCreationTime:
                     firebase.firestore.FieldValue.serverTimestamp(),
               });
         }
      }
   }, [conversationIDs, selectedUser_name]);

   useEffect(() => {
      if (file) {
         const uploading = storage.ref(`files/${file.name}`).put(file);

         if (currentConvID) {
            uploading.on(
               "state_changed",
               (snap) => {
                  const prog = Math.round(
                     (snap.bytesTransferred / snap.totalBytes) * 100
                  );
                  setProgress(prog);
               },
               (err) => {
                  alert(err.message);
               },
               () => {
                  storage
                     .ref("files")
                     .child(file.name)
                     .getDownloadURL()
                     .then((downloadURL) => {
                        db.collection("convs")
                           .doc(currentConvID)
                           .collection("messages")
                           .add({
                              photo: downloadURL,
                              recieverID: selUserID,
                              senderID: currentUserInfo.userID,
                              timestamp:
                                 firebase.firestore.FieldValue.serverTimestamp(),
                           });

                        setFile(null);
                        setProgress(0);
                     });
               }
            );
         } else {
            console.log("select a conversation first!");
         }
      }
   }, [file]);

   useEffect(() => {
      if (selUserID) {
         db.collection("allUsers")
            .doc(selUserID)
            .onSnapshot((snap) => {
               setSelectedUser_name(snap.data().username);
               setSelectedUser_avatar(snap.data().avatarURL);
            });

         if (currentConvID) {
            db.collection("convs")
               .doc(currentConvID)
               .collection("messages")
               .orderBy("timestamp", "asc")
               .onSnapshot((snap) => {
                  setConvMessages(
                     snap.docs.map((doc) => {
                        return {
                           id: doc.id,
                           data: doc.data(),
                        };
                     })
                  );
               });
         }
      }

      db.collection("convs").onSnapshot((snap) => {
         setConversationIDs(
            snap.docs.map((doc) => {
               return doc.id;
            })
         );
      });

      return () => {
         setConvMessages([]);
      };
   }, [selUserID, currentConvID]);

   useEffect(() => {
      messageBoxRef.current.scrollTop = messageBoxRef.current.scrollHeight;
   }, [convMessages]);

   return (
      <div className="messageBox">
         <div className="messageBox__header">
            <div className="header__avatarAndName">
               <span className="avatar">
                  <Avatar
                     type="button"
                     alt={selectedUser_name}
                     src={
                        selectedUser_avatar
                           ? selectedUser_avatar
                           : "/static/images/avatar/2.jpg"
                     }
                  />
               </span>
               <span className="name">
                  <strong>{selectedUser_name}</strong>
               </span>
            </div>
            <div className="header__optionIcon cursor-p">
               <IconButton>
                  <MoreHorizIcon />
               </IconButton>
            </div>
         </div>
         <div
            className="messageBox__body"
            ref={messageBoxRef}
            style={{
               backgroundImage: `url(${process.env.PUBLIC_URL}/whatsapp_bg.jpg)`,
            }}
         >
            <ul>
               {convMessages.map((msg) => {
                  if (msg.data.photo) {
                     return (
                        <li
                           key={msg.id}
                           className={
                              msg.data.senderID === currentUserInfo.userID
                                 ? "reciever_photo"
                                 : "sender_photo"
                           }
                        >
                           <div className="wrapper__image">
                              <div>
                                 <img
                                    src={msg.data.photo}
                                    alt="Failed to load picture"
                                    className="img-fluid text-white"
                                 />
                                 <div className="wrapper__time">
                                    {msg.data.timestamp
                                       ? new Date(
                                            msg.data.timestamp.toDate()
                                         ).toLocaleTimeString()
                                       : null}
                                 </div>
                              </div>
                           </div>
                        </li>
                     );
                  } else {
                     return (
                        <li
                           key={msg.id}
                           className={
                              msg.data.senderID === currentUserInfo.userID
                                 ? "reciever"
                                 : "sender"
                           }
                        >
                           <div className="li__wrapper">
                              <div className="wrapper__message">
                                 {msg.data.message}
                              </div>
                              <div className="wrapper__time">
                                 {msg.data.timestamp
                                    ? new Date(
                                         msg.data.timestamp.toDate()
                                      ).toLocaleTimeString()
                                    : null}
                              </div>
                           </div>
                        </li>
                     );
                  }
               })}
            </ul>
         </div>
         <div className="messageBox__inputField">
            {progress > 0 ? (
               <div className="inputField__progressBar">
                  <progress value={progress} max="100" />
               </div>
            ) : null}
            <div className="inputField__chooseFile">
               <input
                  disabled={progress > 0}
                  type="file"
                  accept="image/jpg, image/png, image/jpeg, image/gif"
                  className="file"
                  ref={fileInputRef}
                  onChange={(e) => setFile(e.target.files[0])}
               />
               <label
                  htmlFor="file"
                  onClick={() => fileInputRef.current.click()}
               >
                  <PhotoIcon />
               </label>
            </div>
            <form className="inputField__inputBox">
               <div className="inputBox">
                  <input
                     type="text"
                     onChange={(e) => setInputText(e.target.value)}
                     value={inputText}
                  />
               </div>
               <button
                  type="submit"
                  className={
                     inputText
                        ? "inputBox__sendBtn text-danger cursor-p"
                        : "inputBox__sendBtn cursor-p"
                  }
                  onClick={handleSendBtn}
                  disabled={!inputText}
               >
                  <SendRoundedIcon />
               </button>
            </form>
         </div>
      </div>
   );
}

export default MessageBox;
