import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import { Avatar } from "@material-ui/core";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import { IconButton } from "@material-ui/core";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";

import { useSelector, useDispatch } from "react-redux";
import { setCurrentUser } from "../redux/actions";

import db from "./Firebase";

function UserList() {
   const { currentUserInfo } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [userLists, setUserLists] = useState([]);
   const [inputUser, setInputUSer] = useState("");

   const [dropDownOpen, setDropDownOpen] = useState(false);
   const [chngUsername, setChngUsername] = useState(false);

   useEffect(() => {
      db.collection("allUsers").onSnapshot((snap) => {
         setUserLists(
            snap.docs.map((doc) => {
               return {
                  id: doc.id,
                  data: doc.data(),
               };
            })
         );
      });
   }, []);

   const optionDropDown = (event) => {
      event.stopPropagation();

      if (dropDownOpen) {
         setDropDownOpen(false);
      } else {
         setDropDownOpen(true);
      }
   };

   const handleChngUsername = (e) => {
      e.preventDefault();

      if (inputUser.length > 0) {
         db.collection("allUsers").doc(currentUserInfo.userID).update({
            username: inputUser,
         });

         dispatch(
            setCurrentUser({
               userID: currentUserInfo.userID,
               email: currentUserInfo.email,
               username: inputUser,
               avatarURL: currentUserInfo.avatar,
               isLoggedIn: currentUserInfo.loggedIn,
            })
         );

         setInputUSer("");
         setChngUsername(false);
      }
   };

   const Logout = () => {
      dispatch(
         setCurrentUser({
            userID: null,
            username: "unknown",
            email: "",
            avatar: "",
            loggedIn: false,
         })
      );
   };

   useEffect(() => {
      window.addEventListener("click", () => {
         setDropDownOpen(false);
      });

      return () => {
         window.removeEventListener("click", () => {
            setDropDownOpen(false);
         });
      };
   });

   return (
      <div className="userList">
         <div className="userList__header">
            <div className="header__avatar-name">
               <span className="avatar-name__avatar">
                  <Avatar
                     type="button"
                     alt={currentUserInfo.username}
                     src={
                        currentUserInfo.avatar
                           ? currentUserInfo.avatar
                           : "/static/images/avatar/2.jpg"
                     }
                  />
               </span>
               <span className="avatar-name__name">
                  {chngUsername ? (
                     <form action="" className="chngUser">
                        <input
                           type="text"
                           onChange={(e) => setInputUSer(e.target.value)}
                           value={inputUser}
                        />
                        <button
                           type="submit"
                           onClick={handleChngUsername}
                           hidden
                        ></button>
                        <span
                           className="chngUser__closeIcon cursor-p text-danger"
                           onClick={() => setChngUsername(false)}
                        >
                           <HighlightOffIcon />
                        </span>
                     </form>
                  ) : (
                     currentUserInfo.username
                  )}
               </span>
            </div>
            <div
               className="header__optionIcon cursor-p"
               onClick={optionDropDown}
            >
               <IconButton>
                  <MoreHorizIcon />
               </IconButton>
            </div>
         </div>
         <div className="userList__list">
            <div
               className={
                  dropDownOpen
                     ? "userList__optionDropDown open"
                     : "userList__optionDropDown close"
               }
            >
               <ul className="optionDropDown__ul">
                  <li onClick={() => setChngUsername(true)}>Change Username</li>
                  <li onClick={Logout}>Log Out</li>
               </ul>
            </div>
            <ul className="allList">
               {userLists.map((userList) => {
                  if (
                     userList.id === currentUserInfo.userID ||
                     userList.data.email === currentUserInfo.email
                  ) {
                     return null;
                  } else {
                     return (
                        <li key={userList.id}>
                           <Link to={`/conv/${userList.id}`}>
                              <div className="allList__singleList">
                                 <span className="singleList__avatar">
                                    <Avatar
                                       alt={userList.data.username}
                                       src={
                                          userList.data.avatarURL
                                             ? userList.data.avatarURL
                                             : "/static/images/avatar/2.jpg"
                                       }
                                    />
                                 </span>
                                 <div className="singleList__name-and-lastMsg">
                                    <strong className="name">
                                       {userList.data.username}
                                    </strong>
                                 </div>
                              </div>
                           </Link>
                        </li>
                     );
                  }
               })}
            </ul>
         </div>
         <div className="userList__creatorName text-center">
            <p>
               Created By <strong>MD Omar</strong>
            </p>
         </div>
      </div>
   );
}

export default UserList;
