import React, { useState, useEffect } from "react";
import { HashRouter, Switch, Route, Redirect } from "react-router-dom";

import "./sass/main.css";
import UserList from "./comps/UserList";
import MessageBox from "./comps/MessageBox";
import SignupLogin from "./comps/Signup_Login";
import PageNotFound from "./comps/PageNotFound";

import { useSelector } from "react-redux";

function App() {
   const { currentUserInfo } = useSelector((state) => state);

   const [windowWidth, setWindowWith] = useState(window.innerWidth);

   /* const pageReloaded = window.performance.navigation.type;

   useEffect(() => {
      // for detecting page reload
      console.log(pageReloaded);
      if (pageReloaded === 1) {
         window.location.pathname = "/chatapp";
      }
   }, []); */

   useEffect(() => {
      window.addEventListener("resize", () => setWindowWith(window.innerWidth));

      return () => {
         window.removeEventListener("resize", () =>
            setWindowWith(window.innerWidth)
         );
      };
   });

   return (
      <HashRouter basename="/chatapp">
         <div className="app container-fluid">
            {currentUserInfo.loggedIn ? (
               <Switch>
                  <Route exact path="/xyz">
                     <h1>xyz page</h1>
                  </Route>
                  <Route exact path="/">
                     {windowWidth > 500 ? (
                        <div className="app__body row">
                           <div className="body__userList col-lg-4 col-md-4 col-12">
                              <UserList />
                           </div>
                           <div className="body__messageBox col-lg-8 col-md-8 col-12">
                              <div className="startConvMsg">
                                 <div className="startConvMsg__msg">
                                    <h2>Welcome to Chat App</h2>
                                    <div>
                                       <h4 className="text-success">
                                          You have logged in successfully !
                                       </h4>
                                       <h6>Let's Start a Conversation !</h6>
                                    </div>
                                    <p>
                                       Created By <strong>MD Omar</strong>
                                    </p>
                                 </div>
                              </div>
                           </div>
                        </div>
                     ) : (
                        <div className="app__body row">
                           <div className="body__userList col-lg-4 col-md-4 col-12">
                              <UserList />
                           </div>
                        </div>
                     )}
                  </Route>
                  <Route exact path="/conv/:selUserID">
                     {windowWidth > 500 ? (
                        <div className="app__body row">
                           <div className="body__userList col-lg-4 col-md-4 col-12">
                              <UserList />
                           </div>
                           <div className="body__messageBox col-lg-8 col-md-8 col-12">
                              <MessageBox />
                           </div>
                        </div>
                     ) : (
                        <div className="app__body row">
                           <div className="body__messageBox col-lg-8 col-md-8 col-12">
                              <MessageBox />
                           </div>
                        </div>
                     )}
                  </Route>
                  <Route
                     exact
                     path="/page_not_found"
                     component={PageNotFound}
                  />
                  <Redirect to="/page_not_found" />
               </Switch>
            ) : (
               <SignupLogin />
            )}
         </div>
      </HashRouter>
   );
}

export default App;
