import { combineReducers } from "redux";

const currentUserInfoObj = {
   userID: null,
   username: "unknown",
   email: "",
   avatar: "",
   loggedIn: false,
};
export const currentUserInfo = (state = currentUserInfoObj, action) => {
   switch (action.type) {
      case "SET_CURRENT_USER":
         return (state = {
            userID: action.userID,
            username: action.username,
            email: action.email,
            avatar: action.avatarURL,
            loggedIn: action.loggedIn,
         });

      default:
         return state;
   }
};
export default combineReducers({ currentUserInfo });
